import logo from './logo.svg';
import './App.css';
import Receiver from './reciever';
import React from 'react'
class App extends React.Component{
  constructor(props){
    super(props)
    this.state = {
      Receiver : new Receiver("CWAPerformanceMetrics"),
      data : []
    }
  }
  render(){
  this.setState({data: this.state.Receiver.getLatestItem()})
  return (
    <div className="App">
      <header className="App-header">
        <p>
          Edit <code>src/App.js</code> and save to reload.
        </p>
          Learn React

      </header>
      <body>
        <p>
          {this.data}
        </p>
      </body>
    </div>
  );
  }
}

export default App;
